<template>
  <div class="info">
    <main class="main">
      <ul class="main__ul">
        <li class="main__li">
          <img
            class="main__img"
            src="static/img/i_age.svg"
            :alt="isChinese ? '' : 'age'"
            :title="isChinese ? '' : 'age'"
          />
          <span class="main__span">{{
            isChinese ? " / 23" : "Age / 23"
          }}</span>
        </li>
        <li class="main__li">
          <img
            class="main__img"
            src="static/img/i_edu.svg"
            :alt="isChinese ? '' : 'education'"
            :title="isChinese ? '' : 'education'"
          />
          <span class="main__span">{{
            isChinese ? " / " : "Undergraduate"
          }}</span>
        </li>
        <li class="main__li">
          <img
            class="main__img"
            src="static/img/i_site.svg"
            :alt="isChinese ? '' : 'location'"
            :title="isChinese ? '' : 'location'"
          />
          <span class="main__span">{{
            isChinese ? " / " : "Shenzhen"
          }}</span>
        </li>
        <li class="main__li">
          <img
            class="main__img"
            src="static/img/i_status.svg"
            :alt="isChinese ? '' : 'status'"
            :title="isChinese ? '' : 'status'"
          />
          <span class="main__span">{{
            isChinese ? " / " : "Status / Quit"
          }}</span>
        </li>
      </ul>
      <div class="introduce">
        <p class="introduce_p">
          {{ isChinese ? aboutRexag.zh_introduce1 : aboutRexag.en_introduce1 }}
        </p>
        <p class="introduce_p">
          {{ isChinese ? aboutRexag.zh_introduce2 : aboutRexag.en_introduce2 }}
          <a class="introduce_a" href="http://www.rexag.site">{{
            isChinese ? "" : "My Blog"
          }}</a>
          {{ isChinese ? aboutRexag.zh_introduce3 : aboutRexag.en_introduce3 }}
          <a class="introduce_a" href="http://www.rexag.site">GitHub</a>
        </p>
      </div>
    </main>
  </div>
</template>
<script>
import { mapGetters } from 'vuex'
export default {
  computed: {
    ...mapGetters({
      isChinese: 'global/rexag/getIsChinese',
      user: 'global/rexag/getUser',
      aboutRexag: 'global/rexag/getAboutRexag'
    })
  }
}
</script>
<style scoped>
.info {
  width: 100%;
  height: 100%;
  background: url(/static/img/bg.png) #109085;
}
.main {
  width: 100%;
  height: calc(100% - 80px);
  position: absolute;
  top: 80px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
.main__ul {
  width: 100vw;
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
}
.main__li {
  width: 50vw;
  height: 80px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.main__img {
  width: 50px;
  height: 50px;
}
.main__span {
  min-width: 50px;
  height: 30px;
}
.introduce {
  width: 90%;
  padding: 20px 5% 50px;
  overflow: scroll;
}
</style>



